<template>
  <div style="overflow: hidden">
    <div class="card">
      <div class="card-body">
        <div class="card-title">
          <h3><font-awesome-icon :icon="['fas', 'users']" /> 用户列表</h3>
          <hr />
        </div>

        <vs-table ref="table" v-model="selected">
          <template #header>
            <vs-input v-model="search" border placeholder="输入用户名" />
          </template>
          <template #thead>
            <vs-tr>
              <vs-th>
                <vs-checkbox :indeterminate="selected.length == users.length" v-model="allCheck" @change="selected = $vs.checkAll(selected, users)" />
              </vs-th>
              <vs-th> 用户名 </vs-th>
              <vs-th> 昵称 </vs-th>
              <vs-th> 手机号 </vs-th>
              <vs-th> 邮箱 </vs-th>
              <vs-th> QQ </vs-th>
            </vs-tr>
          </template>
          <template #tbody>
            <vs-tr :key="i" v-for="(tr, i) in $vs.getPage(users, page, max)" :data="tr" :is-selected="!!selected.includes(tr)">
              <vs-td style="width: 5%" checkbox>
                <vs-checkbox :val="tr" v-model="selected" />
              </vs-td>
              <vs-td>
                {{ tr.uname }}
              </vs-td>
              <vs-td>
                {{ tr.nickname }}
              </vs-td>
              <vs-td>
                {{ tr.phone_number || "未绑定" }}
              </vs-td>
              <vs-td>
                {{ tr.email || "未绑定" }}
              </vs-td>
              <vs-td>
                {{ tr.qid || "未绑定" }}
              </vs-td>

              <!-- <template #expand>
                <div class="con-content">
                  <div>
                    <p>操作 {{ tr.uname }}</p>
                  </div>
                  <div class="d-flex">
                    <vs-button flat icon>
                      <i class="bx bx-lock-open-alt"></i>
                    </vs-button>
                    <vs-button border icon> 修改密码 </vs-button>
                    <vs-button border danger> 删除用户 </vs-button>
                  </div>
                </div>
              </template> -->
            </vs-tr>
          </template>
          <template #footer>
            <vs-pagination v-model="page" :length="$vs.getLength(users, max)" />
          </template>
        </vs-table>

        <h5 class="mt-4 fw-bold d-flex align-items-center">
          操作
          <span class="ms-2 text-danger">已选中 {{ selected.length }} 项</span>
          <vs-button icon border class="ms-auto text-nowrap" @click="refreshData">
            <i class="bx bx-refresh"></i>
          </vs-button>
        </h5>
        <hr />
        <div class="row">
          <button v-wave class="col-md btn btn-primary mb-3 me-3" @click="changeData('pwd')" :disabled="selected.length == 0">
            <font-awesome-icon :icon="['fas', 'lock']" />
            修改密码
          </button>
          <button v-wave class="col-md btn btn-warning mb-3 me-3" @click="changeData('qid')" :disabled="selected.length == 0">
            <font-awesome-icon :icon="['fab', 'qq']" />
            绑定QQ
          </button>
          <button v-wave class="col-md btn btn-success mb-3 me-3" @click="changeData('email')" :disabled="selected.length == 0">
            <font-awesome-icon :icon="['fas', 'envelope']" />
            绑定邮箱
          </button>
          <button v-wave class="col-md btn btn-danger mb-3 me-3" @click="changeData('delete')" :disabled="selected.length == 0">
            <font-awesome-icon :icon="['fas', 'trash']" />
            删除用户
          </button>
        </div>
        <h5 class="mt-2 text-center text-danger">数据无价 谨慎操作</h5>

        <hr />
        <h5>
          操作进度
          <span class="fw-bold">
            <span class="text-primary">{{ progress.all }}</span> | <span class="text-success">{{ progress.finish }}</span> | <span class="text-danger">{{ progress.fail }}</span>
          </span>
        </h5>
        <div class="progress progress-bar-success" style="height: 5px">
          <div role="progressbar" aria-valuemin="0" aria-valuemax="100" aria-valuenow="52" class="progress-bar bg-success" :style="`width: ${progress.all ? (progress.finish / progress.all) * 100 : 0}%;`"></div>
        </div>
      </div>
    </div>
    <div class="card mt-3">
      <div class="card-body">
        <div class="card-title">
          <h3>
            <font-awesome-icon :icon="['fas', 'plus']" />
            添加用户
          </h3>
          <hr />
        </div>

        <vs-input primary v-model="newUser.uname" laceholder="用户名" class="mb-3">
          <template v-if="!newUser.uname" #message-danger> 必填项 </template>
        </vs-input>
        <vs-input danger v-model="newUser.pwd" placeholder="密码" class="mb-3" type="password">
          <template v-if="!newUser.pwd" #message-danger> 必填项 </template>
        </vs-input>

        <vs-input primary v-model="newUser.nickname" placeholder="昵称" class="mb-3"></vs-input>
        <vs-input primary v-model="newUser.phone_number" placeholder="手机号" class="mb-3"></vs-input>
        <vs-input primary v-model="newUser.email" placeholder="邮箱" class="mb-3"></vs-input>
        <vs-input primary v-model="newUser.qid" placeholder="QQ" class="mb-3"></vs-input>

        <button v-wave class="btn btn-success w-100" @click="addUser">
          <font-awesome-icon :icon="['fas', 'plus']" />
          添加用户
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import CryptoJS from "crypto-js/crypto-js";

export default {
  name: "Manage",
  data() {
    return {
      search: "",
      page: 1,
      max: 10,
      allCheck: false,
      users: [],
      selected: [],

      progress: {
        all: 0,
        finish: 0,
        fail: 0,
      },

      newUser: {
        uname: "",
        pwd: "",
        nickname: "",
        phone_number: "",
        email: "",
        qid: "",
      },
    };
  },
  components: {},
  mounted() {
    this.refreshData();
  },
  methods: {
    async changeData(e) {
      if (e === "pwd") {
        this.$swal({
          title: "请输入新密码",
          input: "password",
          inputPlaceholder: "新密码",
          inputValidator: (value) => {
            if (!value) {
              return "密码不能为空";
            }
          },
          confirmButtonText: "确认",
          cancelButtonText: "取消",
          showCancelButton: true,
        }).then((resp) => {
          if (resp.isConfirmed) {
            this.batch(e, resp.value, {
              success: "密码修改成功",
              danger: "密码修改失败",
            });
          }
        });
      } else if (e === "qid") {
        this.$swal({
          title: "请输入需要绑定的QQ号",
          input: "text",
          inputPlaceholder: "QQ号",
          inputValidator: (value) => {
            if (!value) {
              return "QQ号不能为空";
            }
          },
          confirmButtonText: "确认",
          cancelButtonText: "取消",
          showCancelButton: true,
        }).then((resp) => {
          if (resp.isConfirmed) {
            if (resp.isConfirmed) {
              this.batch(e, resp.value, {
                success: "QQ号修改成功",
                danger: "QQ号修改失败",
              });
            }
          }
        });
      } else if (e === "email") {
        this.$swal({
          title: "请输入新邮箱",
          input: "email",
          inputPlaceholder: "邮箱地址",
          confirmButtonText: "确认",
          cancelButtonText: "取消",
          showCancelButton: true,
        }).then((resp) => {
          if (resp.isConfirmed) {
            this.batch(e, resp.value, {
              success: "设置成功",
              danger: "设置失败",
            });
          }
        });
      } else if (e === "delete") {
        this.$swal({
          title: "确认删除？",
          confirmButtonText: "确认",
          cancelButtonText: "取消",
          showCancelButton: true,
        }).then((resp) => {
          if (resp.isConfirmed) {
            this.batch(e, resp.value, {
              success: "删除成功",
              danger: "删除失败",
            });
          }
        });
      }
    },
    async batch(e, value, msgs) {
      this.progress.all = this.selected.length;
      this.progress.finish = 0;
      this.progress.fail = 0;

      for (let i = 0; i < this.selected.length; i++) {
        this.StartChangeData(e, this.selected[i].uname, value).then((resp) => {
          if (resp) {
            this.progress.finish = this.progress.finish + 1;
            this.$toastx({
              type: "success",
              title: msgs.success,
            });
            this.refreshData();
          } else {
            this.progress.fail = this.progress.fail + 1;
            this.$toastx({
              type: "danger",
              title: msgs.danger,
            });
          }
        });
      }
    },
    async StartChangeData(e, uname, value) {
      if (e !== "delete" && e !== "pwd" && e !== "qid" && e !== "email") {
        this.$toastx({
          type: "danger",
          title: "无效操作",
        });
        return false;
      }
      const sign = CryptoJS.SHA256(e + value + uname + "@wszb@").toString(CryptoJS.enc.Hex);
      const res = await this.$store.dispatch("utils/request", {
        method: "post",
        url: "a/setUser",
        data: {
          sign,
          operate: e,
          value,
          uname,
        },
      });
      if (res.data.status === 1) {
        return true;
      } else {
        res.data.msg &&
          this.$toastx({
            type: "warning",
            title: res.data.msg,
          });
        return false;
      }
    },
    async addUser() {
      if (!this.newUser.uname || !this.newUser.pwd) {
        this.$toastx({
          type: "warning",
          title: "请填写必要信息",
        });
        return;
      }
      const res = await this.$store.dispatch("utils/request", {
        method: "post",
        url: "a/addUser",
        data: {
          ...this.newUser,
          sign: CryptoJS.SHA256(this.newUser.uname + this.newUser.nickname + this.newUser.phone_number + this.newUser.qid + this.newUser.pwd + this.newUser.email + "@wszb@").toString(CryptoJS.enc.Hex),
        },
      });
      if (res.data.status === 1) {
        this.$swal({
          icon: "success",
          title: res.data.msg,
        });
        this.refreshData();
      } else {
        this.$swal({
          icon: "error",
          title: res.data.msg,
        });
      }
    },
    async refreshData() {
      this.selected = [];
      const loading = this.$vs.loading({
        target: this.$refs.table,
        text: "加载中...",
      });

      await this.$store
        .dispatch("utils/request", {
          method: "post",
          url: "a/getUserList",
        })
        .then((res) => {
          if (res.data.status === 1) {
            this.users = res.data.data;
          } else {
            throw new Error("获取用户列表失败");
          }
        })
        .catch((err) => {
          this.$toastx({
            type: "danger",
            title: "获取用户列表失败",
          });
          console.log(err);
        });

      loading.close();
    },
  },
};
</script>

<style></style>
